import React from 'react'
import {Link} from 'react-router-dom'
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';

import {CardsData1, CardsData2} from './data'
import './PricingPlan.scss'

const PricingPlan = () => {

    const CardItem = (props) => {
        return (
            <div className='single-card'>
                <div className='logo-container'>
                    <div className='logo'></div>
                    <span>1 user</span>
                </div>
                <h5>{props.category}</h5>
                <div className='price'>
                    <h6>{props.price}<sup>$</sup></h6>
                    <span>per month</span>
                </div>
                <p className='p'>{props.info}</p>
                <div className="features">
                    {props.features.map((l) => {
                        return (
                        <div key={l.text} className="feature-item">
                            {l.icon} 
                            <p className='inner-p'>
                                {l.text}
                            </p>
                        </div>
                        );
                    })}
                </div>
                <div className='last-row'>
                    <Link to={props.to}>{props.link}</Link>
                    <div className='card'>{props.trial}</div>
                </div>
            </div>
        )
    }

    return (
        <div className='shared-pricing-plan'>
            <h5 className='h5' data-aos='fade-left' data-aos-easing="ease-in-sine">Pricing Plan</h5>
            <h1 data-aos='fade-right' data-aos-easing="ease-in-sine">Choose a plan that works for you</h1>
            <Tabs>
                <TabList data-aos='fade-up' data-aos-easing="ease-in-sine">
                    <Tab>Personal</Tab>
                    <Tab>Business</Tab>
                </TabList>
                <div className='tab-panel1'>
                    <div className='bg-img'></div>
                    <TabPanel>
                        {CardsData1.map((l) => (
                            <CardItem key={l.category} {...l} />
                        ))}
                    </TabPanel>
                    <TabPanel>
                        {CardsData2.map((l) => (
                            <CardItem key={l.category} {...l} />
                        ))}
                    </TabPanel>
                </div>
            </Tabs>
        </div>
    )
}

export default PricingPlan
